import './App.css';
import React, {useState} from 'react';
import 'bootstrap/dist/css/bootstrap.css';

function App() {


  const [record, setRecord] = useState({});
  async function serverSend(url, opt){
    return await fetch(url, opt)
  }


  function download(filename, text) {
    let element = document.createElement('a');
    element.setAttribute('href', 'data:application/json;charset=utf-8,' + encodeURIComponent(JSON.stringify(text))
    );
    element.setAttribute('download', filename);
  
    element.style.display = 'none';
    document.body.appendChild(element);
  
    element.click();
  
    document.body.removeChild(element);
  }


  async function handleSubmit(event){
    event.preventDefault();
    const api = "http://127.0.0.1:4000/res/"
    const opt = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ text: record.text})
    };
    let data = await serverSend(api, opt)
    data.json().then(element => download("name.json", element))  
  }


  function handleChange(event){
    event.preventDefault();
    setRecord({
        text: event.target.value,
        completed: false
    })
}


  return (
        <div class="row justify-content-center todo">
            <div class="col-sm-auto">
                    <form  onSubmit={handleSubmit}>
                        
                        <input  type="text" value={record.text} onChange={handleChange}/>
                        <input class="submit" type="submit" value="Vyhledat"/>
                    </form>
            </div>
        </div>
  );
}

export default App;
